import React, { Component } from 'react'
import { Breadcrumb } from 'antd';
export default class Button extends Component {
  render() {
    const routeUrl = this.props.history.location.pathname;
    const routeList = routeUrl.split('/').filter((item)=>item!=='');
    return (
      <div className="animated bounceInRight">
        <Breadcrumb style={{ margin: '16px 0' }}>
          {
            routeList.map((item,index)=>{return(<Breadcrumb.Item key={index}>{item.substring(0,1).toUpperCase()+item.substring(1)}</Breadcrumb.Item>)})
          }
        </Breadcrumb>
        <div className='main '>
          <h1>button</h1>
        </div>
      </div>
    )
  }
}
